.select-container {
    position: relative;
    z-index: 15;

    opacity:0;
}

.form-select{
    width: 100%;
    box-sizing: border-box;

    font-size: 1.1rem;
    letter-spacing: 1px;
    font-family: Bebas Neue;
    text-transform: uppercase;
    padding-top: 0.8rem;
    padding-bottom: 0.7rem;
    padding-left: 0.5rem;
    padding-right: 0.5rem;

    border-radius: 5px;
    border: 3px solid white;
    background: transparent;

    color: white;

    pointer-events: auto;
}

.custom-select{
    text-align: start;
    background: url('./select-arrow.svg') no-repeat;
    background-position: 97% 50%;
    background-size: 20px 20px;
}

.form-dropdownlist{
    position: absolute;
    width: 100%;
    
    height: auto;
    max-height: 150px;
    overflow-y: scroll;

    text-align: start;
    list-style: none;
    text-decoration: none;

    pointer-events: auto;
}

.dropitem{
    font-size: .9rem;
    letter-spacing: 1px;
    font-family: Bebas Neue;
    text-transform: uppercase;

    padding: 0.5rem 0.5rem;
    background-color: black;
    opacity: 0.8;
    border: 1px solid white;
}

.dropitem:hover{
    background-color: gray;
}