.opening-logo {
    position: absolute;
    z-index: 10;

    top: 3%;
    left: 10%;

    pointer-events: none;
    text-align: center;
}

@media (min-width: 550px){
    .opening-logo {
        top: 3%;
        left: 4.5%;
    }
}

#logo-opening {
    opacity: 0.5;
    pointer-events: none;
}

.section-list-dot{
    display: flex;
    list-style: none;

    justify-content: space-between;
    padding-top: 0.5rem;
}

