#fade-screen{
    position: absolute;
    height: 100%;
    width: 100%;

    z-index: 13;

    opacity: 1;
    background-color: black;

    -webkit-transition: opacity 6s ease-out .75s;
    -moz-transition: opacity 6s ease-out .75s;
    -o-transition: opacity 6s ease-out .75s;
    transition: opacity 6s ease-out .75s;

    pointer-events: none;
 
}

.btn-skip{
    position: absolute;
    z-index: 14;

    background: transparent;
    color: white;
    text-transform: uppercase;
    outline: none;
    border: none;

    right: 0;

    font-family: Josefin-Sans Semibold;
    font-size: 1.5rem;

    padding: 1rem 2rem;
}