.formholder{
    position: absolute;

    top: 30%;
    left: 0%;
    right: initial;
    
    z-index: 10;

    max-width: 400px;
    padding-left: 4rem;
    padding-right: 4rem;

    pointer-events: none;
}

.form-word-title{
    font-size: 2rem;
    font-family: Arial Bold;

    text-transform: uppercase;
    letter-spacing: 4px;
    line-height: 40px;

    pointer-events: none;

    opacity:0;

    /*transform: translate(100%,0%);
    transition: transform 1.2 ease-in-out;*/
}

.form-follow {
    text-align: center;
    pointer-events: none,
}

.form-input{
    width: 100%;
    box-sizing: border-box;

    font-size: 1.1rem;
    letter-spacing: 1px;
    font-family: Bebas Neue;
    text-transform: uppercase;
    padding-top: 0.8rem;
    padding-bottom: 0.7rem;
    padding-left: 0.5rem;
    padding-right: 0.5rem;

    border-radius: 5px;
    border: 3px solid white;
    background: transparent;

    color: white;

    pointer-events: auto;

    opacity:0;
}

.mb-sm {
    margin-bottom: 1.75rem;
    pointer-events: none;
}

.form-btn{
    font-size: 1.1rem;
    font-family: Bebas Neue;
    letter-spacing: 1px;
    text-transform: uppercase;

    border-radius: 5px;
    border: 3px solid white;
    background: transparent;

    padding: 0.5rem 3rem;

    color: white;

    background-color: black;
    opacity: 0.8;
    margin-top: 3em;

    pointer-events: auto;

    z-index: 12;

    opacity:0;
}

.form-btn:hover{
    background-color: gray;
}

.form-btn:active{
    background-color: white;
    color: black;
}