#circle{
    position: absolute;
    height: 25px;
    width: 25px;
    border-radius: 50%;
    border: 3px solid white;

    transform: translate(-50%,-50%);
    background: none;

    z-index: 30;

    pointer-events: none;
    opacity: 0;
}

@media (min-width: 550px){
    #circle{
        opacity: 1;
    }
}