.sound-container{
    position: absolute;
    top: 7%;
    right: 10%;

    z-index: 10;
}

.sound-icon{
    height: 35px;
    width: 35px;

    z-index: 10;

    pointer-events: none;
}

.sound-off{
    position: absolute;
    top: 50%;
    left: 50%;

    height: 45px;
    width: 5px;
    transform: translate(-50%,-55%) rotateZ(-45deg);
    background: white;

    z-index: 10;

    pointer-events: none;
}
