.logo-wrapper{
    position: absolute;
	width: 100%;
    height: 100%;
	height: calc(30% + 3vw);
    
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 15;

    pointer-events: none;
}

.logo-video{
	width: 100%;
    height: 100%;

    -webkit-transition: opacity .75s linear;
    -moz-transition: opacity .75s linear;
    -o-transition: opacity .75s linear;
    transition: opacity .75s linear;

    pointer-events: none;
}




