.link-holder{
    position: absolute;
    
    top: 30%;
    left: 0%;
    right: initial;
        
    z-index: 10;
    
    max-width: 400px;
    padding-left: 4rem;
    padding-right: 4rem;
    
    pointer-events: none;

    text-align: center;

    -webkit-user-select: none; /* Safari */        
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* IE10+/Edge */
    user-select: none; /* Standard */
}

.link-btn{
    display: inline-block;

    border-radius: 5px;
    border: 3px solid white;
    background: transparent;

    padding: 0.5rem 3rem;

    margin-top: 2em;

    font-size: 1.1rem;
    font-family: Bebas Neue;
    letter-spacing: 1px;
    text-transform: uppercase;

    pointer-events: auto;

    z-index: 12;

    cursor:auto;

    opacity: 0;
}