.press-start{
    position: absolute;

    z-index: 10;

    left: 50%;
    top: 50%;

    transform: translate(-50%,-50%);

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    pointer-events: none;
    /*border: 1px solid red;*/
}

.start-btn{
    height: 65px;
    width: 65px;
    border-radius: 50%;
    border: 3px solid white;

    transition: opacity 1s ease-in-out;
    outline: none;

    background: none;

    z-index: 25;

    opacity: 0;
    cursor:none;

    pointer-events: none;
}

.press-start-txt{
    position: absolute;

    z-index: 10;

    left: 50%;
    top: 60%;
    transform: translate(-50%,-50%);
    
    pointer-events: none;
}

.start-btn-txt{
    font-size: 1.1rem;
    font-family: Myriad Pro;
    padding-left: 4px;
    letter-spacing: 4px;
    text-transform: uppercase;

    transition: opacity 0.5s ease-in-out;
    color: white;

    text-align: center;

    pointer-events: none;
}

.press-start-scroll{
    position: absolute;

    z-index: 10;

    left: 50%;
    top: 90%;
    transform: translate(-50%,-50%);

    pointer-events: none;

    width: 100%;
}

.start-btn-scroll-txt{
    font-size: 0.8rem;
    font-family: Myriad Pro;
    text-transform: uppercase;
    letter-spacing: 3px;
    pointer-events: none;
    text-align: center;
    opacity:0;
}

@media screen and(min-width:550px) {
    .start-btn-scroll-txt{
        opacity: 1
    }
}
