#opening {
    display: block;
    min-height: 100vh;
    width: 100%;

    position: fixed;
    z-index: 0;

    background-color: #121838;

    color: white;
}

.opening-br{
    position: absolute;

    left: 50%;
    top: 66%;
    transform: translate(-50%,-50%);
    
    -webkit-transition: opacity .75s ease-in-out;
    -moz-transition: opacity .75s ease-in-out;
    -o-transition: opacity .75s ease-in-out;
    transition: opacity .75s ease-in-out;

    z-index: 15;

    pointer-events: none;
}

.br-logo{
    display: none;
    font-family: Josefin-Sans Semibold;
    font-size: 1.5rem;

    text-align: center;
    text-transform: uppercase;
    letter-spacing: 3px;
}

.left-button{
    display: flex;
    justify-content: flex-end;
    align-items: center;

    position: absolute;
    width: 50px;
    height: 50px;

    z-index: 13;
    left: 0%;
    top: 50%;
    transform: translate(-50%,-50%);

    border-radius: 50%;
    background: white;
    border: 1px solid white;

    color: gray;
    font-size: 1rem;
    font-family: Arial Bold;
}

.left-button span {
    margin-right: 7px;
}

.right-button{
    display: flex;
    justify-content: flex-start;
    align-items: center;

    position: absolute;
    width: 50px;
    height: 50px;

    z-index: 13;
    right: 0%;
    top: 50%;
    transform: translate(50%,-50%);

    border-radius: 50%;
    background: white;
    border: 1px solid white;

    color: gray;
    font-size: 1rem;
    font-family: Arial Bold;
}

.right-button span {
    margin-left: 7px;
}
