.progress-container{
    position: absolute;

    left: 50%;
    bottom: 4%;
    transform: translate(-50%,-50%);
    
    transition: opacity .75s ease-in-out;

    z-index: 15;

    pointer-events: none;
}

.progress-txt{
    color:white;
    font-family: Josefin-Sans Semibold;
    font-size: 1.5rem;

    text-align: center;
}